const state = {
    newSocketData: {}, //
    warningData: [], //
    realTimeWarningData: [], //
    deviceStatusResponse:{}, //
    deviceOnline:{},
    isEditFiles:false,//
    isAddNewAlarm:true,//
    carAndGunObj:{},//
    offlineTimeoutArr:[],
    offlineTimeoutObj:{},
    beidouTerminalMes:[],//
    hasCancles:false,//
    followCarOrGun:null,//()
    followInfo:{},//
    packageInoutSocket:{},//
    carParkingDetailSocket:{},//
    socketTalkData:{},
}

const mutations = {

  
    NEWSOCKETDATA(state, obj) {
       
        state.newSocketData=obj;
    },
    PACKAGEINOUTSOCKET(state,obj){
        state.packageInoutSocket=obj;
    },
    CARPARKINGDETAILSOCKET(state,obj){
        state.carParkingDetailSocket=obj;
    },
    SOCKETTALKDATA(state,obj){
        state.socketTalkData=obj;
    },
    DEVICESTATUSRESPONSE(state, obj) {
      
        state.deviceStatusResponse=obj;
    },
    DEVICEONLINE(state, obj) {
      
        state.deviceOnline=obj;
    },
    RESET(state) {
        state.newSocketData = {}
    },
    WARNINGDATA(state) {
        if (state.warningData !== JSON.parse(sessionStorage.getItem('warningData'))) {
            state.warningData = JSON.parse(sessionStorage.getItem('warningData'));
        }
    },
    BEIDOUTERMINALMES(state) {
        if (state.beidouTerminalMes !== JSON.parse(sessionStorage.getItem('beidouTerminalMes'))) {
            state.beidouTerminalMes = JSON.parse(sessionStorage.getItem('beidouTerminalMes'));
        }
    },
    REALTIMEWARNINGDATA(state) {
        if (state.realTimeWarningData !== JSON.parse(sessionStorage.getItem('realTimeWarningData'))) {
            state.realTimeWarningData = JSON.parse(sessionStorage.getItem('realTimeWarningData'));
        }
    },
    ISEDITFILES(state,val){
      state.isEditFiles=val 
    },
    ISADDNEWAlARM(state,val){
        state.isAddNewAlarm=val 
      },
    CARANDGUNOBJ(state,val){
        state.carAndGunObj=val
    },
    OFFLINETIMEOUTARR(state, arr) {
        state.offlineTimeoutArr=arr


    },
    OFFLINETIMEOUTOBJ(state, obj) {
        state.offlineTimeoutObj=obj


    },
    HASCANCLES(state,val){
        state.hasCancles=val 
    },
    FOLLOWCARORGUN(state,val){
        state.followCarOrGun=val 
    },
    FOLLOWINFO(state,val){
        state.followInfo=val 
    },

}

const actions = {
   
    SAVE_NEWSOCKETDATA({
        commit
    }, obj) {
        commit('NEWSOCKETDATA', obj)
    },
    SAVE_PACKAGEINOUTSOCKET({
        commit
    }, obj) {
        commit('PACKAGEINOUTSOCKET', obj)
    },
    SAVE_CARPARKINGDETAILSOCKET({
        commit
    }, obj) {
        commit('CARPARKINGDETAILSOCKET', obj)
    },
    SAVE_SOCKETTALKDATA({
        commit
    }, obj) {
        commit('SOCKETTALKDATA', obj)
    },
    
    SAVE_DEVICESTATUSRESPONSE({
        commit
    }, obj) {
        commit('DEVICESTATUSRESPONSE', obj)
    },
    SAVE_DEVICEONLINE({
        commit
    }, obj) {
        commit('DEVICEONLINE', obj)
    },
    RESET_DEVICESTATUSRESPONSE({
        commit
    }) {
        commit('DEVICESTATUSRESPONSE',{})
    },
    RESET_DEVICEONLINE({
        commit
    }) {
        commit('DEVICEONLINE',{})
    },
    RESET_NEWSOCKETDATA({
        commit
    }) {
        commit('NEWSOCKETDATA',{})
    },
    SAVE_WARNINGDATA({
        commit
    }) {
        commit('WARNINGDATA')
    },
    SAVE_REALTIMEWARNINGDATA({
        commit
    }) {
        commit('REALTIMEWARNINGDATA')
    },
    SAVE_BEIDOUTERMINALMES({
        commit
    }) {
        commit('BEIDOUTERMINALMES')
    },
}



export default {
    namespaced: true,
    state,
    mutations,
    actions
}