import axios from 'axios'
import {
    Message,
} from 'element-ui'

import store from '@/store'
import router from './router'


const service = axios.create({
    timeout: 1000*60*2, // request timeout
    baseURL: '/gate'



})
let pending = []; //ajaxajax
let cancelToken = axios.CancelToken;
let removePending = (config) => {
    for(let p in pending){
        if(pending[p].u === config.url + '&' + config.method) { //
            pending[p].f(); //
            pending.splice(p, 1); //
        }
    }
}
// 
service.interceptors.request.use(
    config => {
        // removePending(config); //ajax

        // if(!config.cancelToken){
        //     config.cancelToken = new cancelToken((c)=>{
        //         // ajax&
        //         pending.push({ u: config.url + '&' + config.method, f: c });  
        //     });
        // }
       
        
        if (store.getters.token !== '' && store.getters.token !== undefined) {
            config.headers['Authorization'] = "Bearer " + store.getters.token

        };
        if(config.url.indexOf('StandardApiAction_getVideoFileInfo.action')>-1){// 
            // config.url=config.url.slice(6);
           return config;
        };
        // timeoutjsession
        if(config.url.indexOf('/proxy/')>-1){// 
            // config.timeout = 0;
            if(config.url.indexOf('login')<0){// 
                config.data.jsession = sessionStorage.jsession;
            };
        };
        if (config.url.indexOf('/verify/') < 0&&config.url.indexOf('/noapp/')<0&&config.url.indexOf('/eoms/')<0) { // app
            if(config.url[0]=='/'){
                config.url='/app'+config.url

            }else{
                config.url='/app/'+config.url
            }
        }else if(config.url.indexOf('/verify/') < 0&&(config.url.indexOf('/noapp/')!==-1)){///app
            config.url=config.url.slice(6);
        }
        //application/json,request
        if (config.headers['content-type'] == 'application/x-www-form-urlencoded' || config.headers['content-type'] == undefined) {
            //formdata
            var formData = new FormData();
            for (var key in config.data) {
                formData.append(key, config.data[key])
            }
            config.data = formData;
        } else if (config.headers['content-type'] == 'application/json') {
            return config;
        }
       
       

        return config;




    },
    error => {
        console.log(error) // for debug
        return Promise.reject(error)
    }
)
// 
service.interceptors.response.use(
    response => {
        // removePending(response.config);  //ajaxpending
        let tipMes = "";
        const res = response.data;
        if(res.result!=undefined){// 
            res.status = res.result;
            if (res.status == 1) res.status == 100;// 1
            let errMsg = {
                'N0':'',
                'N100':'',
                'N2':'',
                'N3':'',
                'N4':'', 
                'N5':'', 
                'N6':'', 
                'N7':'', 
                'N8':'',
                'N9':'', 
                'N10':'', 
                'N11':'', 
                'N12':'', 
                'N13':'', 
                'N14':'',
                'N15':'', 
                'N16':'', 
                'N17':'', 
                'N18':'', 
                'N19':'', 
                'N20':'', 
                'N21':'',
                'N24':'', 
                'N25':'', 
                'N26':'', 
                'N27':'', 
                'N28':'',
                'N32':'',//
            };
            res.msg = errMsg[`N${res.result}`];
        };
        //10001treeselect10004
        // 403
        // res.status=401;
        if ((res.status != 0&& res.code != 200 && res.status != 1 && res.status != 200&& (res.status != undefined||res.code != undefined)) || (res.code == 403)|| (res.status == 403)|| (res.code == 401)|| (res.status == 401)) {
            if (res.code === 403||res.status === 403||res.code === 401||res.status === 401) {
                store.dispatch('user/resetToken')
                router.push({
                    path: "/login"
                });

            return;
            }
            //
            if(/.*[\u4e00-\u9fa5]+.*$/.test(res.msg)){
                tipMes=res.msg;
            }
            Message({
                // message: res.msg,    
                message: tipMes,
                type: 'error',
                duration: 5 * 1000
            })

            return Promise.reject('error')
        } else {
             //
             if(!(/.*[\u4e00-\u9fa5]+.*$/.test(res.msg))){
                res.msg = "";
            }
            return res
        }
    },
    error => {

       
        let tipMes = "";
        // allLoading.close();
        if(axios.isCancel(error)) {  //
            return error;              //return,resundefined
        }
        if(error.response.status==401){
            store.dispatch('user/resetToken')
            router.push({
                path: "/login"
            });

        return;
        }
        //,
        if(!(error.response.config.url.indexOf('thirdparty/gaode/regeo')!==-1&&location.hostname.indexOf('beidoulab.work')!==-1)){
            Message({
                message: tipMes,
                type: 'error',
                duration: 5 * 1000
            })
        }
       

        
        return Promise.reject(error)
    }
)
export default service